@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{box-sizing:border-box}html{height:100%;background-color:#F5F6F8}body{font-family:"roboto" ,Arial, Helvetica, sans-serif;font-size:16px}h1,h2{font-weight:bold}h1{font-size:3rem}h2{font-size:2rem}label{font-size:1.5rem;display:block}button{padding:4px 12px;margin-left:8px;background-color:#f8f8f8;border-radius:4px;border:1px solid #d3d0d0}.container{height:100%;padding:96px 160px}@media screen and (max-width: 790px){.container{padding:96px 32px}}.disabled{pointer-events:none;background-color:#F5F6F8;opacity:0.5}.card{padding:1.5rem;margin:1rem 0;background-color:white;border:1px solid rgba(0,0,0,0.125);border-radius:3px}.card__list{margin-top:1rem;list-style:disc;margin-left:2rem}.card__list-item{margin:0.5rem}.card__title{display:inline-block}.search-bar{width:100%}.search-bar__input{width:100%;padding:0.5rem 0.5rem;border:1px solid #cacaca;border-top-right-radius:3px;border-bottom-right-radius:3px;border-left:none}.search-bar__input:focus{outline:none}.search-bar__container{margin-top:0.5rem;display:flex;justify-content:center;align-items:center}.search-bar__submit{border-radius:3px;border-top-right-radius:0;border-bottom-right-radius:0;border-right:none;padding:10px;height:33px;width:2rem;background:transparent url(/the-shoppies/static/media/loupe.28b0ad10.svg) no-repeat center;background-size:1rem}.results,.nominations{display:inline-block;width:49%}@media screen and (max-width: 980px){.results,.nominations{margin-left:0;margin-right:0;width:100%}}.results{margin-right:1%}.nominations{vertical-align:top;margin-left:1%}.banner{background-color:#28a74585;border:1px solid #28a745}.spinner{border:5px solid #dbdbdb;border-top:5px solid #3498db;border-radius:50%;width:1.5rem;height:1.5rem;-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite;display:inline-block}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

